body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-size: 62.5%;
}

div {
  box-sizing: border-box;
}

p {
  margin: 0;
}

body {
  font-size: 1.6rem;
  background-color: #FAFAFA;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.post {
    background-color: #FFF;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
    margin-top: 60px;
    margin-bottom: 60px;
    width: 600px;
    font-size: 1.4rem;
    text-align: left;
}

span {
    font-weight: 700;
    margin-right: 5px;
}

.post-header {
    width: 100%;
    border: 1px solid lightgray;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px;
}

.post-header__thumbnail {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 10px;
}

.post-image {
    width: 100%;
    height: auto;
}

.respond {
    padding-top: 12px;
    padding-bottom: 7px;
}

.likes {
    margin-bottom: 2px;
}

p {
    margin: 8px 0;
}

.content {
    width: 100%;
    border: 1px solid lightgray;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    align-items: stretch;
}

.fa-heart, .fa-comment {
    cursor: pointer;
    font-size: 2.3rem;
    font-stretch: expanded;
}

.fa-comment {
    margin-left: 15px;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1)
}

.fas {
    color: red;
}
.timestamp {
    margin-top: 5px;
    margin-bottom: 15px;
    font-size: 1.1rem;
    font-weight: 600;
    color: gray;
}

.commentform {
    padding: 15px 0;
    border-top: 1px solid lightgray;
}

.commentform input {
    width: 100%;
    border: none;
    padding: 5px 0;
    font-size: 1.4rem;
}

.commentform input:focus {
    outline: none;
}
.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid lightgray;
    background-color: white;
}

.logo {
    width: 212px;
    padding: 19px 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.logo .fa-instagram {
    font-size: 3.4rem;
    margin-right: 5px;
}

.empty-div {
    height: 32px;
    border-right: 1px solid gray;
}

.logo img {
    margin-right: 3px;
    width: 113px;
    height: auto;
}

.searchbar {
    text-align: center;
    width: 211px;
    height: 24px;
    border: 1px solid lightgray;
    justify-self: center;
    padding: none;
    font-size: 1.4rem;
    border-radius: 3px;
    background-color: #FAFAFA;
}

.header .login {
    width: 215px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 40px;
}

.header .login img {
    width: 22px;
    height: 20px;
}

.header .login img:hover {
    cursor: pointer;
}
::-webkit-input-placeholder {
    color: lightgray;
}

::-ms-input-placeholder {
    color: lightgray;
}

::placeholder {
    color: lightgray;
}

.loginpage {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.loginpage form {
    box-sizing: border-box;
    padding: 25px 0;
    background-color: #FFF;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 350px;
    height: 360px;
    border: 1px solid lightgray;
}

.loginpage form img {
    margin: 5px 0;
    width: 180px;
}

.loginpage form input {
    margin: 3px 0;
    padding: 5px;
    width: 270px;
    height: 40px;
    font-size: 1.2rem;
    background-color: #FAFAFA;
    border: 1px solid #ededed;
    border-radius: 3px;
    box-sizing: border-box;
}

.loginpage form input:focus {
    outline: none;
}

.loginpage form button {
    width: 270px;
    height: 30px;
    padding: 0;
    font-size: 1.4rem;
    font-weight: bold;
    color: #FFF;
    margin: 8px 0;
    background-color: #CBE0F8;
    border: none;
    border-radius: 5px;
}

.loginpage form .orfacebook {
    margin: 10px 0;
    width: 270px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.loginpage form .orfacebook-line {
    width: 110px;
    border-top: 1px solid lightgray;
}

.loginpage form .orfacebook-or {
    font-size: 1.4rem;
    font-weight: bold;
    color: lightgray;
}

.loginpage .facebook {
    margin: 10px 0;
}

.forgotpassword {
    margin-top: 5px;
    font-size: 1.25rem;
    color: #003366;
}
